@import "../../partial/scss/base";

@mixin checkbox-init {
	@include checkbox-tokens;
	@include checkbox-appearance;
	@include checkbox-label;
	@include checkbox-checked;
	@include checkbox-colors;
	@include checkbox-sizes;
	@include checkbox-states;
}
@mixin checkbox-tokens {
	--checkbox-background: var(--color-secondary-01);
	--checkbox-font-weight: var(--font-weight-medium);
	--checkbox-size: 24px;
	--checkbox-icon-size: var(--icon-size-sm);
	--checkbox-bullet-position: calc(
		(var(--checkbox-size) - var(--checkbox-icon-size)) * 0.5
	);
	--checkbox-padding: var(--spacing-scale-base);
}
@mixin checkbox-appearance {
	display: block;
	& + & {
		margin-top: var(--spacing-scale-base);
	}
	// Remover o checkbox padrão do navegador
	input {
		opacity: 0;
		position: absolute;
		+ label {
			cursor: pointer;
			display: inline-block;
			min-height: var(--checkbox-size);
			padding-left: calc(var(--checkbox-padding) + var(--checkbox-size));
			position: relative;
			&::before {
				background: var(--checkbox-background);
				border: 1px solid v(color-secondary-04);
				border-radius: 4px;
				content: "";
				height: var(--checkbox-size);
				left: 0;
				position: absolute;
				top: -1px;
				width: var(--checkbox-size);
			}
			&:empty {
				min-height: 20px;
				min-width: var(--checkbox-size);
				padding: 0;
				&::before {
					top: 0;
				}
			}
		}
	}
}
@mixin checkbox-label {
	label {
		color: var(--text-color);
		font-size: var(--font-size);
		font-weight: var(--checkbox-font-weight);
	}
	&.hidden-label {
		label {
			padding-left: var(--checkbox-size);
			text-indent: -10000px;
			white-space: nowrap;
			width: 0;
		}
	}
}
@mixin checkbox-checked {
	input {
		&:checked + label {
			&::after {
				// background: var(--selected);
				border-color: var(--selected);
				border-style: solid;
				border-width: 0 3px 3px 0;
				content: "";
				content: "";
				height: var(--checkbox-icon-size);
				left: 8px;
				position: absolute;
				right: 0;
				top: 4px;
				transform: rotate(45deg);
				width: 8px;
			}
		}
	}
}
@mixin checkbox-states {
	input {
		// Inválid
		&:invalid {
			+ label::before {
				border-color: var(--color-danger);
			}
		}
		//Focus
		&:focus,
		&:focus-visible,
		&.focus-visible {
			&:checked + label::before,
			+ label::before {
				@include focus-shadow;
			}
		}
		//hover
		&:hover:not(:disabled) {
			+ label::before {
				background-image: linear-gradient(
					rgba(var(--interactive-rgb), var(--hover)),
					rgba(var(--interactive-rgb), var(--hover))
				);
			}
		}
		// Disabled
		&:disabled + label {
			@include disabled;
		}
	}
}
@mixin checkbox-colors {
	@each $state, $color in (valid, success), (invalid, danger) {
		&.is-#{$state},
		&.#{$state},
		&[#{$state}] {
			input {
				+ label::before {
					border-color: var(--color-#{$color});
				}
			}
		}
	}
}
@mixin checkbox-sizes {
	// Versão pequena
	&.is-small,
	&.small,
	&[small] {
		input {
			// Texto
			+ label {
				line-height: var(--spacing-scale-2xh);
				min-height: var(--spacing-scale-2xh);
			}
			// Caixa
			+ label::before {
				height: var(--spacing-scale-2xh);
				width: var(--spacing-scale-2xh);
			}
			// Checado
			&:checked + label::after {
				border-width: 4px;
				height: 10px;
				width: 10px;
			}
		}
	}
}

.#{$prefix}checkbox {
	@include checkbox-init;
}
